.caption {
  color: var(--text-color);
  font-weight: 600;
  font-size: 25px;
  font-family: var(--poppins);
  margin-bottom: 4px;
}

.btn{
  font-size: 14px;
  padding: 6px 60px;
  border-radius: 5px;
  color: white;
  font-weight: 600;
  letter-spacing: 0.2px;
}

.red-btn {
    background: var(--red-btn-bg);
}

.no-btn {
    color: var(--primary-text-color);
    border: 1px solid var(--primary-color);
}