.naac-table thead {
  background: #d2ddeb;
  color: var(--text-color);
}
.naac-table thead th {
  padding: 10px;
  text-align: left;
  white-space: nowrap;
}
.naac-table thead td:nth-child(1),.naac-table thead th:nth-child(1),
thead tr:nth-child(1) {
  border-radius: 10px 0px 0px 0px;
}
.naac-table thead td:last-child,
.naac-table thead th:last-child,
.thead tr:last-child {
  border-radius: 0px 10px 0px 0px;
}
.naac-table {
  border-collapse: separate;
  border-spacing: 0 8px;
  width: 100%;
}
.naac-table td {
  text-align: left;
  padding: 10px;
  color: var(--text-color);
}
.naac-table tbody tr {
  padding: 10px;
  /* background: red; */
}

.red-row {
  background: rgba(249, 49, 23, 0.205);
}
.green-row {
  background: #7aba903d;
}
.activate-btn {
  background: #7aba90;
  padding: 10px;
  border-radius: 7px;
  color: #fff;
  font-weight: 700;
}
.disable-btn {
  background: #e24356;
  padding: 10px;
  border-radius: 7px;
  color: #fff;
  font-weight: 700;
}

.table-scroll::-webkit-scrollbar{
  width: 10px;
  background: transparent;
}
.table-scroll::-webkit-scrollbar-thumb{
  background: var(--text-color);
  border-radius: 6px;
}

/* Blur rows */
.naac-table tbody.blue-rows tr {
  background: #ECF0F8;
  color: var(--text-color);
}
.naac-table tbody.blue-rows tr:hover {
  background: #d2ddeb;
}