

    .naac-red-btn, .naac-green-btn, .naac-outlined-btn,
    .naac-dark-blue-btn, .naac-warning-btn, .naac-danger-btn, .naac-dark-green-btn {
    font-size: 14px;
    padding: 6px 24px;
    border-radius: 5px;
    color: white;
    font-weight: 600;
    letter-spacing: 0.2px;
    white-space: nowrap;
}

.naac-red-btn {
    background:var(--red-btn-bg);
}


.naac-green-btn{
    background: var(--green-btn-bg);
}

.naac-outlined-btn{
color: var(--primary-text-color);
border: 1px solid var(--primary-color);
}

.naac-dark-blue-btn{
background: var(--add-btn-bg);
color: white;
}

.naac-danger-btn{
    background: var(--danger);
}
.naac-warning-btn{
    background: var(--warning);
}
.naac-dark-green-btn{
    background: var(--dark-green);
}