.sidebar-container{
    background: linear-gradient( 50deg,#174A84,#143499) ;
    width: 100%;
    max-width: 250px;
    height: 100vh;
    font-weight: 500 ;
    color: #fff;
    transition: 0.3s all ease-in-out;
    top: 0;
    left: 0;
    z-index: 100;

}
ul.side-bar li {
    display:flex;
    justify-content: center;
    align-items: center;
}
.nav-item{
    padding:  7px 10px;
    border-radius: 4px;
    width:100%;
    display:flex;
    font-weight: 700;

}
.nav-item:hover,
.nav-item.active{
    background: #fff;
    padding:  7px 10px;
    border-radius: 4px;
    color: #174A84;

}
.sidebar-icons {
    filter: brightness(0) invert(100%);
}
.nav-item:hover  .sidebar-icons, .nav-item.active  .sidebar-icons { 
filter: brightness(0) saturate(100%) invert(23%) sepia(35%) saturate(3346%) hue-rotate(195deg) brightness(82%) contrast(87%);
}

.sidebar-container.mobile-sidebar{
    display: block !important;
}
.side-bar-wrapper ul li span {
    pointer-events: none;
    font-size: 13px;
    white-space: nowrap;
}
.mobile-sidebar .side-bar-wrapper ul li span {
    display: block !important;
}
.mini-sidebar .side-bar-wrapper ul li span {
    display: none !important;
}
.mini-sidebar{
    width: 5% !important;
}

