.container {
  max-width: 1200px;
}
.heading {
  font-family: var(--poppins);
  font-size: 21px;
  font-weight: 600;
  color: var(--text-color);
}
.dashboard-main-container {
  background: #f4e6e9;
  height: 300px;
  width: 100%;
  border-radius: 7px;
}
.dashboard-main-heading {
  font-weight: 600;
  font-family: var(--poppins);
  color: #d4565d;
}
.dashboard-sub-heading {
  color: var(--text-color);
  font-weight: 700;
  text-transform: capitalize;
}

.dashboard-sub-text {
  color: black;
  font-weight: 500;
  text-transform: none;
}

.summery-container {
  padding: 10px;
  border-radius: 20px;
  height: 100px;
}
.summery-count {
  background: rgba(255, 255, 255, 0.137);
  padding: 10px;
  border-radius: 15px;
  color: #fff;
  font-weight: 700;
  width: 90px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.summery-count:nth-child(5),
.summery-count:nth-child(6) {
  background: rgb(58 54 54 / 14%) !important;
}
.summery-title {
  color: #fff;
  font-weight: 700;
  font-family: var(--manrope);
  /* margin-left: 55px; */
}

.critetion-drop {
  width: 100%;
}
.criterion-container {
  border: 1px solid var(--text-color);
  border-radius: 7px;
}
.criterion-container p {
  color: var(--text-color);
  font-weight: 600;
  margin-left: 10x;
}

/* @media (max-width: 400px) {
    .dashboard-manin-container div{
        width:50%;
    }
}  */
 @media (min-width: 400px) and (max-width: 768px) {
   .dashboard-main-container {
    height: 200px;
  } 
}
@media (max-width: 768px) {
    .dashboard-main-container {
        height: 150px;
      }
  .dashboard-sub-heading {
    font-weight: normal;
  }
}

@media (min-width: 1600px) {
  .container {
    max-width: 95%;
  }
}
