.dropdown-bg {
    background:  var(--white);
    background: #ffffff;
    position: absolute;
    left: 0;
    z-index: 5;
    width: 100%;
    height: 250px;
    overflow: auto;
    box-shadow: 0 0 3px 0px black;
    border-radius: 0 0 7px 7px;
}
.dropdown-bg ul li  {
    padding: 0 12px;
    cursor: pointer;
    white-space: nowrap;
}
.dropdown-bg ul li:hover  {
    background: var(--light-gray);
}
.dropdown-bg ul li:not(:last-child) {
    border-bottom: 1px solid rgba(0, 0, 0, 0.089);
}

.enabled-clubs{
    background: var(--light-gray);
    padding: 10px;
    border-radius: 7px;
}