/* SCROLL BAR */
/* width */
.naac-scroller::-webkit-scrollbar {
  width: 5px;
}

/* Track */
.naac-scroller::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
}
 
/* Handle */
.naac-scroller::-webkit-scrollbar-thumb {
  background: var(--text-color); 
  border-radius: 10px;
}

