.filter-drop{
    border-radius: 5px;
    width: 180px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.filter-drop-options{
    position: absolute;
    right: 0;
    background: var(--text-color);
    color: #fff;
    border-radius: 7px;
    margin-top:10px;
    padding: 5px 20px;
}
 .border-white{
    border-bottom: 1px solid white;
}
.filter-drop-options .filter-options{
    padding: 10px;
    background: var(--text-color);
    color: #fff;
    cursor: pointer;
}
.filter-drop-options .filter-options:hover{
    background: #fff;
    color: var(--text-color);
    border-radius: 7px;
}
.filter-options{
    color: var(--text-color);
}