
.naac-modal-overlay{
    padding: 15px;
    width: 100%;
    min-height: 100vh;
    overflow: hidden;
    position: absolute;
    background: rgba(var(--overlay-bg),54%);
    z-index: 100;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    pointer-events: none;
    
}
.naac-modal{
    padding: 15px;
    background: #fff;
    border-radius: 10px;
    overflow: auto;
    pointer-events: auto;
}

/* width */
.naac-modal::-webkit-scrollbar {
    width: 5px;
  }
  
  /* Track */
  .naac-modal::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 10px;
  }
   
  /* Handle */
  .naac-modal::-webkit-scrollbar-thumb {
    background: var(--text-color); 
    border-radius: 10px;
  }
  

.naac-modal-lg{
    max-width: 1100px;
    width: 70%;
}

.naac-modal-md{
   max-width: 1100px;
    width: 100%;
}
  
.naac-modal-header{
    width: 100%;
    display: flex;
    justify-content: space-between;
    position: relative; 
}
.naac-modal-header .close {
    position: absolute;
    right: 0;
    top: 0;
}

.naac-modal-header .naac-main-heading {
    color: var(--text-color);
    font-weight: 600;
    font-size: 18px;
    font-family: var(--poppins);
    margin-bottom: 4px;
}
.naac-modal-header .naac-sub-heading{
    color: var(--text-color);
    font-weight: 600;
    font-size: 10px;
    font-family: var(--poppins);
}

.naac-modal-body{
    width: 100%;
    margin: 30px 0px;
}

.naac-modal-footer {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.naac-modal-footer .naac-modal-footer-btn{
    margin-right: 10px;
}
.modal-label-field{
    background:#FFFFFF !important;

}


/* MEDIA QUERIES */
/* // Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) { 
    .naac-modal-md{
        max-width: 1100px;
         width: 100%;
     }
     .naac-modal{
         padding: 30px;
     }
     .naac-modal-header .naac-sub-heading{ 
        font-size: 13px;
    }
     
 }

/* // Medium devices (tablets, 768px and up) */
@media (min-width: 768px) { 
    
 }

/* // Large devices (desktops, 992px and up) */
@media (min-width: 992px) { 
    .naac-modal-md{
        max-width: 1100px;
         width: 70%;
     }
     .naac-modal{
        padding: 30px;
    }
 }

/* // X-Large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) { 

    .naac-modal-md{
        max-width: 1100px;
         width: 50%;
     }
 }

/* // XX-Large devices (larger desktops, 1400px and up) */
