

.logo{
    width: 100%;
}
.login-page-wrapper{
    background: #EDF1FE;
}
.login-left-pane{
    background-image: -o-linear-gradient(45deg,#1E78AD,#133398);
    background-image: linear-gradient(45deg,#1E78AD,#133398);

}
.login-form-container{
    width: 100%;
    max-width: 75%;
}
.login-btn{
    padding: 10px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    background: #3C58B8;
    width: 100%;
    color: white;
    border-radius: 7px;
}
.forgot-password{
    color: #B00000;
}
.sign-up-btn{
    border: 1px solid #5C4FB0 ;
    color: #3C58B8;
    padding: 5px 12px;
    border-radius: 4px;
-webkit-transition: 0.2s background ease-in-out ;
-o-transition: 0.2s background ease-in-out ;
transition: 0.2s background ease-in-out ;
}
.sign-up-btn:hover{
    color: #fff;
    background:#5C4FB0 ;
}
.content-wrapper{
    width:250px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.left-text{
    white-space: nowrap;
}
@media(max-width:1140px){
    .login-form-container{
        max-width: 100%;
    }
    .left-text{
        width: 100%;
    }

}
@media( max-width:768px){
    .logo{
        width: 100px ;
    }
    .left-text{
        width: 60%;
        margin-left: 27px;
        white-space: normal;
        color: #1B317E;
        font-weight: 600;
    }
    .login-left-pane{
        background: transparent;
    }
    .right-pane{
        height: 20%;
    }
}