
  /* sleleton elements */
 .loader-table .skeleton , .loader-table  .skeleton-head {
    background: linear-gradient(
      90deg,
      hsl(210, 15%, 88%),
      hsl(210, 15%, 95%),
      hsl(210, 15%, 88%)
    );
    background-size: 200%;
    animation: loader 1s infinite reverse;
    width: 100%;
    height: 50px;
    border-radius: 7px;
    padding: 10px;
    margin-bottom: 10px;
  }
 .loader-table  .skeleton-head{
    border-radius: 10px 10px 0px 0px;
  }

  /* skeleton animation */
  @keyframes loader {
    from {
      background-position: -100% 0;
    }
    to {
      background-position: 100% 0;
    }
  }
