.topbar-container {
    background: transparent;
    width: 100%;
}
.search-box-container {
    border: 1px solid #90A1B5;
    padding: 7px 10px;
    border-radius: 5px;
}

.criterion-selected-item{
    background: var(--text-color);
    color: white;
}

.critetion-drop{
    color: var(--text-color) ;
    font-weight: 700;
}

.criterion-sub-drop{
    background: #174a84da ;
    color: white ;
}

.profile-btn{
    background: #EAF0F6;
    border-radius: 30px;
    width: 70px;
    height: 35px;
    padding: 0px 5px;
    display:flex;
    justify-content: space-between;
    align-items: center;
}
.profile-btn img {
    width: 30px;
    height: 30px;
    border-radius: 50%;
}

.search-box-container input:active,.search-box-container input:focus-visible{
    /* border: 1px solid rgb(43, 38, 73); */
    outline: none;
    border: none;
  }
.search-box-container input::-webkit-input-placeholder{

    color:#174A84
}
.search-box-container input::-moz-placeholder{

    color:#174A84
}
.search-box-container input:-ms-input-placeholder{

    color:#174A84
}
.search-box-container input::-ms-input-placeholder{

    color:#174A84
}
.search-box-container input::placeholder{

    color:#174A84
}

.hamburger{
    background: var(--text-color);
    height: 4px;
    width: 25px;
    border-radius: 10px;
    margin-right: 10px;
}
.hamburger::before,
    .hamburger::after{
    content: "";
    position: absolute;
    top:7px;
    left:0;
    height: 4px;
    background: var(--text-color);
    width: 20px;
    border-radius: 10px;
    margin-left: 0.5rem;

}
.hamburger::after{
    top: 25px !important;
    width: 20px;
    margin-left: 0.5rem;

}


.criterions-options , .user-scope{
    width: 100%;
    font-family: "Poppins";
    white-space: nowrap;
    font-weight: 500;
}
.criterions-options:hover, .user-scope:hover {
    color: white;
    background: var(--text-color);
    border-radius: 6px;
}
.profile-options{
    width: 100%;
    display:flex;
    justify-content: start;
    font-family: "Poppins";
    white-space: nowrap;
    padding: 16px;
    color: var(--text-color);
    font-weight: 500;
}
.profile-options:hover{
    color: white;
    background: var(--text-color);
    border-radius: 6px;
}

@media ( max-width:768px){
    .search-box-container {
        width: 100%;
    }
}