.thead{
    background: #D2DDEB;
    color: var(--text-color);
}
.thead th {
        white-space: nowrap;
}
.thead th:not(:first-child) {
    padding: 10px;
    text-align: left;
}
.thead th:nth-child(1),thead tr:nth-child(1){
    border-radius: 10px 0px 0px 0px;
}
.thead th:last-child, .thead tr:last-child{
    border-radius: 0px 10px 0px 0px;
}
.user-table{
border-collapse:separate; 
border-spacing: 0 1em;
}
.user-table td {
    padding: 10px;
    color: var(--text-color);
}
.user-table tr td:first-child{
    text-align: center;
}
.user-table tbody tr{
    padding: 10px;
    /* background: red; */
}

.red-row {
    background: rgba(249, 49, 23, 0.205);
}
.green-row {
    background: #7aba903d;
}
.activate-btn{
    background: #7ABA90;
    padding: 10px;
    border-radius: 7px;
    color: #fff;
    font-weight: 700;
}
.disable-btn{
    background: #E24356;
    padding: 10px;
    border-radius: 7px;
    color: #fff;
    font-weight: 700;
}