.add-new-field-modal{
    width: 100%;

}


@media (min-width:350px){
    .add-new-field-modal{
        width: 75%;
    }
}

@media (min-width:567px){
    .add-new-field-modal{
        width: 50%;
        max-width: 50%;
    }
}

@media (min-width:1200px){
    .add-new-field-modal{
        width: 25%;
        max-width: 25%;
    }
}