.error-container{
    background: #e60023c0;
    border-radius: 0 0 4px 4px;
    color: #e7dddeee;
    font-weight: 600;
    font-family: var(--monrope);
    transition: 1s all ease-in-out;
    transform: translateY(-450px);
}
.active{
     transform: translateY(0);
}

@media( max-width:768px){
 .active{
    transform: translateY(-340px);
    /* position: absolute; */
 }
}