.static-fields-wrapper{
    padding: 10px 25px;
    background: var(--light-gray);
    border-radius: 7px;
}
.optional-fields-wrapper{
    padding: 10px 25px;
}   
.static-fields-wrapper h1, .optional-fields-wrapper h1{
    color: var(--text-color);
    font-weight: 700;
    margin-bottom: 10px;
}
.static-fields-wrapper label ,.optional-fields-wrapper label{
    color: var(--text-color);
    font-weight: 500;
}

.modal-index{
    color: var(--text-color);
    font-family: var(--poppins);
    font-weight: 600;
}


/* / */

.add-field-modal{
    width: 100%;
    height: 90vh;
}


@media (min-width:768px){
    .add-field-modal{
        width: 60%;
        max-width: 75%;
    }
}

